let glass =function(qs="suggestCount=0&suggestChosen=false&clickSource=searchBtn&typedKeyword=information+technology&sc.keyword=information+technology&locT=C&locId=1146608&jobType="){
    return new Promise(function(resolve,reject)
      {
        let qa=qs.split('&');
        let qss = qa.map(item=>`args[${item.split('=')[0]}]=${item.split('=')[1]}`).join("&")
        var req = new XMLHttpRequest();
        req.open('GET', `test.php`, true);
        req.onreadystatechange = function () {
          if (req.readyState == 4) {
            if(req.status == 200)
              {
                //   resolve(JSON.parse(req.responseText));
                let $ = cheerio.load(req.responseText)
                let out = []
                $('li.jl').each((i,e)=>{
                    let tmp ={}
                    let $$ = cheerio.load(e)
                    tmp['link']=$$('a.jobLink').attr('href')
                    tmp['title']=$$('a.jobLink').text()
                    tmp['logo']=$$('.sqLogo>img').attr('data-original')
                    out.push(tmp)
                })
                resolve(out)
              }
            else
              reject(Error(req.statusText));
          }
        };
        req.onerror = function() {
          reject(Error("network error"));
        };
        req.send(`${qss}&html=y&s=ul.jlGrid&u=https://www.glassdoor.com/Job/jobs.htm`);
      });
  };
   export default glass


