import React from "react"
let job=(item,k)=>{
   // console.log(item)
   let td=k=>(k%1==1)?"col-md-3 col-md-offset-1 jl":"col-md-3 j1"
    return(
        
        <div key={k} className={td(k)}>
             <a href={`https://glassdoor.com${item.link}`}><img src={item.logo} alt="" className="col-md-4"/><h4 className="col-md-8">{item.title} </h4></a>
        </div>
    )
}
let Jobs=props=>{
    let j = props.j || [{logo:'',link:'',title:''}];
    let out=[]
    for(let item in j){
        out.push(job(j[item],item))
    }
    return <div className="row jobs">{out}</div>
}
export default Jobs


