import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
//import getData from "./get"
import glass from "./glass"
import Jobs from './job'
class App extends Component {
  state={test:{},ts:""}
  componentDidMount(){
    // getData("https://reactphp.azurewebsites.net/getinfo.php")
    // .then(test=>{this.setState({test})})
  glass().then(data=>this.setState({test:data}))
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">php React Test</h1>
        </header>
        <p className="App-intro">
         <h3>ICT Jobs</h3>
        </p>
        <div className="container-fluid">
        <div className="row">
        <div className="col-md-10 col-md-offset-1"><Jobs j={this.state.test}/></div></div>
        </div>
        
        
      </div>
    );
  }
}

export default App;



